import { graphql, Script, ScriptStrategy, useStaticQuery } from "gatsby";
import * as React from "react";

import { SITE_URL } from "../../../nubarchiva/settings/metadata";
import { getAbsoluteUrl } from "../shared/utils/getAbsoluteUrl";

type PropsPostMarkupSchema = {
  title: string | null | undefined;
  slug: string | null | undefined;
  thumbnail: string | null | undefined;
  datePublished: string | null | undefined;
  dateModified: string | null | undefined;
  description: string | null | undefined;
};
const siteUrlThumbnail = `${SITE_URL}assets/thumbnails/`;
const siteUrlImages = `${SITE_URL}assets/images/`;
const PostMarkupSchema: React.FC<PropsPostMarkupSchema> = (props: PropsPostMarkupSchema) => {
  const image = props?.thumbnail ? getAbsoluteUrl(props.thumbnail) : getAbsoluteUrl("home.png", siteUrlThumbnail);
  const markup = `
{
  "@context": "https://schema.org",
  "@type": "BlogPosting",
  "mainEntityOfPage": {
    "@type": "WebPage",
    "@id": "${getAbsoluteUrl(`${props.slug}`)}"
  },
  "headline": "${props.title}",
  "image": "${image}", 
  "author": {
    "@type": "Organization",
    "name": "nubarchiva"
  },
  "publisher": {
    "@type": "Organization",
    "name": "nubarchiva",
    "logo": {
      "@type": "ImageObject",
      "url": "${getAbsoluteUrl("logo.png", siteUrlImages)}"
    }
  },
  "datePublished": "${props.datePublished}",
  "dateModified": "${props.dateModified}",
  "description": "${props.description}
}

  	`;
  return (
    <>
      <Script strategy={ScriptStrategy.postHydrate} type="application/ld+json">
        {markup}
      </Script>
    </>
  );
};

export default PostMarkupSchema;
