import { graphql, Link, PageProps } from "gatsby";
import * as React from "react";
import ReactHtmlParse from "react-html-parser";

import { Language } from "../../../nubarchiva/settings/translations";
import BannerIcons from "../../modules/BannerIcons/BannerIcons";
import CardAuthor from "../../modules/CardAuthor/CardAuthor";
import TitleResumeAndForm, { RenderForm } from "../../modules/content/TitleResumeAndForm/TitleResumeAndForm";
import Layout from "../../modules/Layout/Layout";
import PostMarkupSchema from "../../modules/SEO/PostMarckupSchema";
import { SEO } from "../../modules/SEO/SEO";
import { getLangByRelativeDirectory, getSlugWithLang } from "../../modules/shared/utils/getSlugWithLang";

const PagePost = (props: PageProps<Queries.postPageQuery>) => {
  return (
    <Layout
      landingName={"post"}
      lang={props.pageContext.lang}
      navbarLinksList={[...props.data.navbarLinks.nodes.map((item) => item.metas)]}
      legalLinks={props.data.allLegals?.nodes?.map((item) => item.childMarkdownRemark?.frontmatter) || []}
    >
      <div className="docs">
        <div className="post__headLine">
          <h1 className={"post__h1"}>{props.data.markdownRemark?.frontmatter?.h1}</h1>
          <ul className={"post__info"}>
            <li>
              <Link to={getSlugWithLang(`blog`, props.pageContext.lang)}> Blog </Link>
            </li>
            {props.data.markdownRemark?.frontmatter?.categoryList?.map((item) => (
              <li key={item?.metas?.slug}>
                <Link to={getSlugWithLang(`blog/${item?.metas?.slug}`, props.pageContext.lang)}>
                  {" "}
                  {item?.metas?.name}{" "}
                </Link>
              </li>
            ))}
          </ul>
        </div>
        <div className={"doc"}>{ReactHtmlParse(`${props.data.markdownRemark?.content_html}`)}</div>
        <div className="post__footer">
          {props.data.markdownRemark?.frontmatter?.authorList?.map((item, index) => (
            <CardAuthor
              key={index}
              summary={{ ...item?.summary, socialNetworkList: item?.profile?.socialNetworkList }}
              photo={item?.photo.sharp.gatsbyImageData}
            />
          ))}
        </div>
      </div>
      <BannerIcons isRelative={true} />
      <TitleResumeAndForm
        data={{ title: "Sé parte del cambio", tagTitle: "h3" }}
        nameSection={"subscribe-post"}
        renderForm={RenderForm.SUBSCRIBE}
      />
    </Layout>
  );
};

export const Head = (props: PageProps<Queries.postPageQuery>) => (
  <>
    <SEO
      title={props.data.markdownRemark?.frontmatter?.metas?.title}
      description={props.data.markdownRemark?.frontmatter?.metas?.description}
      thumbnailWithExtension={props.data.file?.publicURL}
      canonical={`blog/${props.data.markdownRemark?.frontmatter?.metas?.slug}`}
      lang={props.pageContext.lang as Language}
      isNotOriginalContent={props.data.markdownRemark?.frontmatter?.metas?.isNotOriginalContent}
      slug={`blog/${props.data.markdownRemark?.frontmatter?.metas?.slug}`}
      hrefLangList={props.data?.hrefLangsList?.nodes?.map((node) => ({
        hrefLang: getLangByRelativeDirectory(node.relativeDirectory),
        slug: `/blog/${node.md[0].frontmatter?.metas?.slug}`,
      }))}
    />
    <PostMarkupSchema
      title={props.data.markdownRemark?.frontmatter?.metas?.title}
      slug={`blog/${props.data.markdownRemark?.frontmatter?.metas?.slug}`}
      thumbnail={props.data.file?.publicURL}
      datePublished={`${props.data.markdownRemark?.frontmatter?.datePublished}`}
      dateModified={`${props.data.markdownRemark?.frontmatter?.dateModified}`}
      description={props.data.markdownRemark?.frontmatter?.metas?.description}
    />
  </>
);

export default PagePost;

export const query = graphql`
  #  query postPage($id: String!, $settingsId: String!, $lang: String = "es_es") {
  query postPage($id: String!, $lang: String = "es_es", $settingsId: String!) {
    markdownRemark(id: { eq: $id }) {
      ...seo
      frontmatter {
        h1
        datePublished
        dateModified
        categoryList {
          metas {
            slug
            name
          }
        }
        authorList {
          profile {
            name
            bio_html
            socialNetworkList
          }
          summary {
            title
            description
          }
          photo {
            sharp: childImageSharp {
              gatsbyImageData
            }
          }
        }
      }
      content_html: html
      id
    }
    navbarLinks: allPage(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          name
          slug
        }
      }
    }
    file(name: { eq: $settingsId }, ext: { regex: "/.png|.jpg|.jpeg/" }) {
      publicURL
    }

    hrefLangsList: allFile(
      filter: { childrenMarkdownRemark: { elemMatch: { frontmatter: { settings: { id: { eq: $settingsId } } } } } }
    ) {
      nodes {
        md: childrenMarkdownRemark {
          frontmatter {
            metas {
              slug
            }
          }
        }
        relativeDirectory
      }
    }
    allLegals: allFile(
      filter: { sourceInstanceName: { eq: "legal" }, relativeDirectory: { eq: $lang } }
      sort: { childMarkdownRemark: { frontmatter: { order: ASC } } }
    ) {
      nodes {
        childMarkdownRemark {
          frontmatter {
            slug
            name: title
          }
          id
        }
        relativeDirectory
      }
    }
  }
`;
