import { GatsbyImage } from "gatsby-plugin-image";
import * as React from "react";
import { PropsWithChildren } from "react";
import ReactHtmlParse from "react-html-parser";
import { useTranslation } from "react-i18next";

import SocialLinksList from "../SocialsLinks/SocialLinksList";

type ICardAuthor = {
  title: string;
  description: string;
  socialNetworkList: string[];
};

type PropsCardAuthor = PropsWithChildren<{
  summary: ICardAuthor;
  photo: any;
}>;

const CardAuthor: React.FC<PropsCardAuthor> = (props: PropsCardAuthor) => {
  const { t, i18n } = useTranslation("global");

  const { summary, photo } = props;
  return (
    <div className={`m-card-author`}>
      <div className="c-card-author">
        <GatsbyImage
          className={"card-author__photo"}
          alt={`${t("photoProfile")}`}
          image={photo}
          objectFit={"cover"}
          objectPosition={"center"}
        />
        <div>
          <p className={"card-author__name"}>{summary.title}</p>
          <div className="card-author__info">
            <div>{ReactHtmlParse(`${summary.description}`)}</div>
            <SocialLinksList
              settings={{ colorIcon: "blue" }}
              socialLinkList={summary.socialNetworkList.map((item) => {
                return {
                  url: item,
                };
              })}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default CardAuthor;
