import * as React from "react";
import { PropsWithChildren } from "react";
import { Trans, useTranslation } from "react-i18next";

//import { close_svg } from "../../../Modals/elements_modal";
import { StateForm } from "../../useForm";
import { check_svg } from "../elements_form";

export type SubmitType = {
  stateForm: StateForm;
  textButtonSubmit: string;
  textButtonSubmitting: string;
  textInfo?: string;
};

type PropsSubmit = PropsWithChildren<{ submit: SubmitType }>;

const Submit: React.FC<PropsSubmit> = (props: PropsSubmit) => {
  const { t } = useTranslation("global");
  return (
    <div className={"m-submit"}>
      <button
        type="submit"
        disabled={props.submit.stateForm > 0}
        className={`a-button 
        ${props.submit.stateForm === StateForm.SUCCESS ? "a-button--success" : ""}
        ${props.submit.stateForm === StateForm.SUBMITTING ? "a-button--submitting" : ""}
        ${props.submit.stateForm === StateForm.FAIL ? "a-button--fail" : ""}
        `}
      >
        {props.submit.stateForm === StateForm.REST && <> {props.submit.textButtonSubmit}</>}
        {props.submit.stateForm === StateForm.SUBMITTING && <> {props.submit.textButtonSubmitting} </>}
        {props.submit.stateForm === StateForm.SUCCESS && (
          <>
            {" "}
            {t("Submit.Completed")} {check_svg}{" "}
          </>
        )}
        {props.submit.stateForm === StateForm.FAIL && <> {t("Submit.Error")} </>}
      </button>
      {props.submit.stateForm === StateForm.FAIL && (
        <p className={"m-submit__sms m-submit__sms--fail"}>
          <Trans t={t} components={{ b: <b /> }}>
            {t("Submit.ErrorMessage")} {props.submit.textInfo}
          </Trans>
        </p>
      )}
      {props.submit.stateForm === StateForm.SUCCESS && (
        <p className={"m-submit__sms m-submit__sms--success"}>
          <Trans t={t} components={{ b: <b /> }}>
            {t("Submit.CompletedMessage")} {props.submit.textInfo}
          </Trans>
        </p>
      )}
    </div>
  );
};

export default Submit;
