import * as React from "react";
import ReactHtmlParse from "react-html-parser";

import { IData } from "../../../types/IData";
import DoNotMountComponent from "../../DoNotMountComponent/DoNotMountComponent";
import { TypeForm } from "../../Forms/domain/Forms";
import { Demo } from "../../Forms/Views/demo/Demo";
import { Info } from "../../Forms/Views/Info/Info";
import { Subscribe } from "../../Forms/Views/subscribe/Subscribe";
import withForm from "../../Forms/withForm";

export const enum RenderForm {
  DEMO = "demo",
  INFO = "info",
  SUBSCRIBE = "subscribe",
}

type PropsTitleResumeAndForm = {
  data: IData;
  nameSection: string;
  renderForm?: RenderForm;
};

const TitleResumeAndForm: React.FC<PropsTitleResumeAndForm> = ({
  nameSection,
  data,
  renderForm,
}: PropsTitleResumeAndForm) => {
  if (!renderForm) return <DoNotMountComponent sms={"Falta form"} />;

  let form;

  switch (renderForm) {
    case RenderForm.SUBSCRIBE:
      form = Subscribe;
      break;
    case RenderForm.INFO:
      form = Info;
      break;
    case RenderForm.DEMO:
      form = Demo;
      break;
    default:
      form = Info;
      break;
  }

  const FormUse = withForm(form, TypeForm.INFO, nameSection);
  const title_render = React.createElement(data.tagTitle || "p", { className: "a-title-section" }, data.title);
  return (
    <section className={`s-${nameSection} o-title-resume-form`}>
      <div className={`c-title-resume-form`}>
        <div className={"a-title-section"}>{title_render} </div>
        {data.resume_html && <div className={"title-resume-form__text"}>{ReactHtmlParse(data.resume_html)}</div>}
        <div className="title-resume-form__form">
          <FormUse />
        </div>
      </div>
    </section>
  );
};

export default TitleResumeAndForm;
