import * as React from "react";
import { PropsWithChildren } from "react";
import { useTranslation } from "react-i18next";

import Submit, { SubmitType } from "../../components/Submit/Submit";
import { StateForm } from "../../useForm";

export type PropsForm = PropsWithChildren<{
  handleSubmit(event: React.FormEvent<HTMLFormElement>): void;
  stateForm: StateForm;
  fromLanding: string;
  infoRequest: string;
}>;

export const Info: React.FC<PropsForm> = (props: PropsForm) => {
  const { t } = useTranslation("global");
  const infoSubmit: SubmitType = {
    textButtonSubmit: `${t("form.Send")}`,
    textButtonSubmitting: `${t("form.Sending")}`,
    stateForm: props.stateForm,
    textInfo: props.infoRequest,
  };
  return (
    <form
      name="contact"
      method="post"
      data-netlify="true"
      onSubmit={(event) => {
        props.handleSubmit(event);
      }}
      className={""}
    >
      <input type="hidden" name="form-name" value="Contacto" />
      <div className={"form__columns"}>
        <div>
          <label hidden>{t("form.PlaceholderName")}</label>
          <input
            className={"a-input a-input--required"}
            type="text"
            name="name"
            placeholder={`${t("form.PlaceholderNameSubname")}`}
            required
          />
          <span>*</span>
        </div>
        <div>
          <label hidden>{t("form.PlaceholderEmail")} </label>
          <input
            className={"a-input a-input--required"}
            type="email"
            name="email"
            placeholder={`${t("form.PlaceholderEmail")}`}
            required
          />
          <span>*</span>
        </div>
        <div>
          <label hidden>{`${t("form.PlaceholderPhone")}`} </label>
          <input
            className={"a-input a-input--required"}
            type="tel"
            name="phone"
            placeholder={`${t("form.PlaceholderPhone")}`}
            required
          />
          <span>*</span>
        </div>
        <label hidden> {t("form.PlaceholderOrg")} </label>
        <input className={"a-input"} type="text" name="company" placeholder={`${t("form.PlaceholderOrg")}`} />
      </div>
      <div className={"m-info-form__one-line a-input--required"}>
        <label hidden>{t("form.TextMessage")} </label>
        <input
          className={"a-input"}
          type="text"
          name="message"
          placeholder={`${t("form.PlaceholderMessage")}`}
          required
        />
        <span>*</span>
      </div>

      <div className={"m-info-form__check-submit"}>
        <div className={"m-info-form__private"}>
          <label htmlFor={`politica--${props.fromLanding}`} className={"a-policy"}>
            {t("form.politic")}{" "}
            <a
              href="/legal/politica-de-privacidad/"
              className={"a-link"}
              target="_blank"
              rel="nofollow noopener noreferrer"
            >
              {t("form.politicLink")}
            </a>{" "}
          </label>
          <input
            type="checkbox"
            name={`politica--${props.fromLanding}`}
            id={`politica--${props.fromLanding}`}
            value="acepta-politica"
            required
          />
        </div>
        <div className="center">
          <Submit submit={infoSubmit} />
        </div>
      </div>
    </form>
  );
};
